#wrapper {
    margin: 0 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#container {
    margin: 0;
    color: black;
    background-color: white;
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    flex-direction: column;
    transform-origin: top left;
    scale: .80;
    width: 1720px;
    border: 1px solid #ddd;
    border-radius: 12px;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, .2);
    overflow: hidden;
    scroll-behavior: none;
    z-index: 1;
}

#mainContent {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
}

#topContainer {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
}

#vizContainer {
    margin: 0;
    padding: 0;

    #tableauBlocker {
        background-color: white;
        position: relative;
        top: -25px;
        height: 25px;
        z-index: 1;
    }
}

#sidebar {
    min-width: 210px;
    margin: 0 3px 0 12px;
    z-index: 1;
}