 #container {
     background-color: #f7f7f7;
     margin: 12px 0 0 0;
     display: flex;
     flex-direction: column;
     padding: 12px;
     border-radius: 12px;
     box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
 }

 .shelf {
     display: flex;
     flex-direction: row;
     justify-content: space-between;
     align-items: center;
     height: 60px;
     padding: 12px;
     border-radius: 12px;
     box-sizing: border-box;
     box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
 }

 .hasTooltip {
     position: relative;
 }

 #faFilePdf {
     color: #973126;
 }

 #faFilePdf:hover {
     color: #007097;
     transition: all 0.1s ease-in-out;
 }

 #faFileExcel {
     color: #0a9544;
 }

 #faFileExcel:hover {
     color: #007097;
     transition: all 0.1s ease-in-out;
 }

 #faFilePowerpoint {
     color: #e74c3c;
 }

 #faFilePowerpoint:hover {
     color: #007097;
     transition: all 0.1s ease-in-out;
 }

 .icon {
     font-size: 30pt;
     cursor: pointer;
 }

 .tool {
     color: #ccc;
     font-size: 15pt;
     cursor: pointer;
 }

 .tool:hover {
     color: #007097;
     transition: all 0.1s ease-in-out;
 }

 .tool:active {
     color: #005077;
     transition: all 0.1s ease-in-out;
 }

 #resetDashboard {
     color: black;
     background-color: white;
     margin: 12px 0;
     height: 60px;
     display: flex;
     flex-direction: row;
     justify-content: center;
     align-items: center;
     font-weight: bold;
     font-size: 15pt;
     border: none;
     border-radius: 12px;
     box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
     cursor: pointer;

     p {
         font-size: 24px;
         margin: -3px 12px 0 0;
     }
 }

 #resetDashboard:hover {
     box-shadow: 0 3px 4px 1px rgba(0, 0, 0, .18);
     transition: all 0.1s ease-in-out;
 }

 #resetDashboard:active {
     box-shadow: inset 0 2px 2px 1px rgba(0, 0, 0, .18);
     transition: all 0.1s ease-in-out;
 }