#container {
    box-sizing: border-box;
    background-color: rgb(12, 12, 35);
    color: rgb(121, 128, 171);
    text-align: center;
    width: 100%;
    flex-shrink: 0;

    a {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 6px 0;
        text-decoration: none;
        color: rgb(121, 128, 171);

        p {
            padding: 0 12px 0 0;
            position: relative;
            top: 12px;
        }
    }

    p {
        margin: 0 0 6px 0;
        color: rgb(121, 128, 171)
    }
}